import { Observable, from } from 'rxjs';
import { mergeMap, distinctUntilChanged, share, startWith } from 'rxjs/operators';
import { getUrlParameters } from './get-url-parameters';
const getRootThemedElement = () => document.querySelector('[class^=theme_]');
export const setTheme = (theme) => {
    const className = `theme_${theme}`;
    const rootThemedElement = getRootThemedElement();
    rootThemedElement.classList.forEach(className => {
        if (className.startsWith('theme_')) {
            rootThemedElement.classList.remove(className);
        }
    });
    rootThemedElement.classList.add(className);
};
export const getTheme = (themedElement) => {
    return [...themedElement.classList.values()]
        .find(className => className.startsWith('theme'))
        ?.split('_').pop();
};
const getInitialTheme = () => {
    const { theme } = getUrlParameters();
    return theme;
};
const fromMutationObserver = (target, options) => new Observable(subscriber => {
    const mutationObserver = new MutationObserver((mutationsList) => {
        subscriber.next(mutationsList);
    });
    mutationObserver.observe(target, options);
    return mutationObserver.disconnect;
});
export const theme$ = fromMutationObserver(getRootThemedElement(), { attributes: true, childList: false, subtree: false })
    .pipe(mergeMap((mutationRecords) => {
    const themes = mutationRecords
        .map(({ target }) => getTheme(target));
    return from(themes);
}), distinctUntilChanged(), startWith(getInitialTheme()), share());
